@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-color: #e1e4e8;
  --text-color: #000000;
  --highlight-color: #2727e6;
  --wave-color: #9e9e9e;
  --letter-highlight: orangered;
}

.dark-mode {
  --main-color: #000000;
  --text-color: #ffffff;
  --highlight-color: #ff4141;
  --wave-color: #363d49;
  --letter-highlight: orangered;
}

body {
  font-family: Inter;
  font-size: 24px;
  background-color: #e1e4e8;
  background-color: var(--main-color);
  color: #000000;
  color: var(--text-color);
}

.main {
  background-image: url(/Portfolio/static/media/grid.6a6f83a3.svg);
  background-position: center center;

  height: 100vh;
  transform: translate(0, 0);
  transition: transform 0.5s;
}

.move-body {
  transform: translate(-100px, 0);
}

.hero {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

h3 {
  font-family: 'Anonymous Pro', monospace;
  color: var(--text-color);
  font-size: 1.8vw;
}

@media screen and (max-width: 480px) {
  h3 {
    font-family: 'Anonymous Pro', monospace;
    color: var(--text-color);
    font-size: 2vw;
  }
}

.hero-text {
  margin-top: 200px;
  vertical-align: middle;
  align-items: center;
  align-self: center;
  left: 10%;
  position: fixed;
}

.hero-text h3 {
  border-right: 0.15em solid var(--highlight-color);
  white-space: nowrap;
  overflow: hidden;
}
.hero-text h3:nth-child(1) {
  width: 100%;
  -webkit-animation: type2 2s steps(40, end);
  animation: type2 2s steps(40, end);
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.hero-text h3:nth-child(2) {
  width: 100%;
  opacity: 0;
  -webkit-animation: type2 2s steps(40, end);
  animation: type2 2s steps(40, end);
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.hero-text h3:nth-child(3) {
  width: 100%;
  opacity: 0;
  -webkit-animation: type2 2s steps(40, end);
  animation: type2 2s steps(40, end);
  -webkit-animation-delay: 4s;
  animation-delay: 4s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.hero-text h3:nth-child(4) {
  width: 100%;
  opacity: 0;
  -webkit-animation: type2 2s steps(40, end);
  animation: type2 2s steps(40, end);
  -webkit-animation-delay: 6s;
  animation-delay: 6s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.hero-text h3:nth-child(5) {
  width: 33%;
  opacity: 0;
  -webkit-animation: type3 0.6s steps(12, end),
    blink 0.5s step-end infinite alternate;
  animation: type3 0.6s steps(12, end), blink 0.5s step-end infinite alternate;
  -webkit-animation-delay: 8s;
  animation-delay: 8s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

@keyframes type {
  0% {
    width: 0;
  }
  99.9% {
    border-right: 0.15em solid var(--highlight-color);
  }
  100% {
    border: none;
  }
}

@-webkit-keyframes type {
  0% {
    width: 0;
  }
  99.9% {
    border-right: 0.15em solid var(--highlight-color);
  }
  100% {
    border: none;
  }
}

@keyframes type2 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  99.9% {
    border-right: 0.15em solid var(--highlight-color);
  }
  100% {
    opacity: 1;
    border: none;
  }
}

@-webkit-keyframes type2 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  99.9% {
    border-right: 0.15em solid var(--highlight-color);
  }
  100% {
    opacity: 1;
  }
}

@keyframes type3 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes type3 {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}
@-webkit-keyframes blink {
  50% {
    border-color: transparent;
  }
}

.container {
    width: 300px;
    border-radius: 10px;
    position: fixed;
    top: 16px;
    right: 180px;
}

.inner {
    background: var(--highlight-color);
    padding: 1em;
    border-radius: 10px;
    width: 250px;
    -webkit-clip-path: circle(8% at 90% 20%);
            clip-path: circle(8% at 90% 20%);
    transition: -webkit-clip-path .5s ease-in-out;
    transition: clip-path .5s ease-in-out;
    transition: clip-path .5s ease-in-out, -webkit-clip-path .5s ease-in-out;
    cursor: pointer;
    color: white;
}

.inner:hover {
    -webkit-clip-path: circle(75%);
            clip-path: circle(75%);
}

.inner:hover span {
    opacity: 0;
}

.container .inner span {
    float: right;
    font-weight: bold;
    position: relative;
    margin-right: 1%;
    transition: opacity .2s ease-in-out;
    font-size: 0.8em;
}
.header {
  position: fixed;
  top: 32px;
  left: 32px;
  display: flex;
  flex-direction: row;
  z-index: 4;
  justify-content: center;
}

.avatar {
  width: 5vw;
  height: 5vw;
  background-image: url(/Portfolio/static/media/profile_pic.4e116130.jpg);
  border-radius: 50%;
  margin-right: 20px;
  object-fit: contain;
  border: 2px solid var(--highlight-color);
}

.title {
  display: flex;
  flex-direction: column;
}

.title .designation {
  font-size: 0.8vw;
  line-height: 0;
  margin-left: 4px;
}

h1 {
  align-items: center;
  font-size: 2vw;
  line-height: 0;
}
h1 span {
  color: var(--letter-highlight);
}

@media screen and (max-width: 480px) {
  h1 {
    display: none;
  }

  .header {
    position: fixed;
    top: 32px;
    left: 32px;
    display: flex;
    flex-direction: row;
    align-items: center;
    z-index: 4;
  }
}

@media screen and (max-width: 770px) {
  .header {
    position: fixed;
    top: 32px;
    left: 32px;
    display: flex;
    flex-direction: row;
    align-items: center;
    z-index: 4;
  }
}

h1 rect {
  fill: var(--highlight-color);
}

a {
  color: var(--text-color);
  text-decoration: none;
}

a:hover {
  color: var(--highlight-color);
}

a.dark-mode-toggle {
  position: fixed;
  top: 32px;
  right: 100px;
  display: flex;
  align-items: center;
  z-index: 3;
}

a.dark-mode-toggle circle {
  fill: currentColor;
}

a.dark-mode-toggle rect {
  stroke: currentColor;
}

a.menu-toggle {
  position: fixed;
  top: 32px;
  right: 32px;
  display: flex;
  align-items: center;
  z-index: 5;
}

a.menu-toggle rect {
  fill: currentColor;
}

svg {
  vertical-align: middle;
}

a.dark-mode-toggle g.toggle circle {
  transition: transform 0.5s;
}
a.dark-mode-toggle:hover g.toggle circle {
  transform: scale(1.75);
}

div.wipe {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 0%;

  background-color: var(--highlight-color);
  z-index: 5;
  transition: background-color 0.0001s 2s;
}

nav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  max-width: 500px;
  height: 100vh;
  background-color: var(--main-color);
  padding-left: 48px;

  transform: translate(100%, 0);
  transition: transform 0.5s 0s;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 4;
}

@media screen and (max-width: 480px) {
  nav {
    position: fixed;
    top: 0;
    right: 0;
    width: 90%;
    height: 100vh;
    background-color: var(--main-color);

    transform: translate(100%, 0);
    transition: transform 0.5s 0s;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 4;
  }

  nav div.internal {
    font-size: 24px;
    line-height: 32px;
    flex: 1 1;
  }

  nav div.external {
    margin-bottom: 24px;
  }
}

@media screen and (max-width: 770px) {
  nav {
    position: fixed;
    top: 0;
    right: 0;
    width: 90%;
    max-width: 400px;
    height: 100vh;
    background-color: var(--main-color);

    transform: translate(100%, 0);
    transition: transform 0.5s 0s;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 4;
  }

  nav div.internal {
    font-size: 36px;
    line-height: 48px;
    flex: 1 1;
  }

  nav div.external {
    margin-bottom: 36px;
  }
}

.nav-open {
  transform: translate(0, 0);
  transition-delay: 0.2s;
}

nav div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

nav div.internal {
  font-size: 48px;
  line-height: 64px;
  flex: 1 1;
}

nav div.external {
  margin-bottom: 48px;
}

.fake-mid {
  background-color: var(--highlight-color);
  transform: translate(0, 0);
  transition-delay: 0.1s;
}

.fake-back {
  background-color: var(--text-color);
  transform: translate(0, 0);
  transition-delay: 0s;
}

.fake-mid-close {
  background-color: var(--highlight-color);
  transform: translate(100%, 0);
  transition-delay: 0.1s;
}

.fake-back-close {
  background-color: var(--text-color);
  transform: translate(100%, 0);
  transition-delay: 0.2s;
}

